<template>
  <div class="complaintDiagnosis">
    <div class="top-content content-border">
      <div class="content-title">
        <div class="content-title-left">
          <span class="block"></span>
          <span>客户第2次通知的可接受度分析</span>
        </div>
        <div class="content-title-right"></div>
      </div>
      <div class="chart-main content-table">
        <bdaTable
          :tableData="acceptanceData"
          :headerColor="'#1899fa'"
          :isPage="true"
          :pageConfig="acceptancePageConfig"
          @changePage="changePage"
        ></bdaTable>
      </div>
    </div>

    <div class="center-content content-border">
      <div class="content-title">
        <div class="content-title-left">
          <span class="block"></span>
          <span>客户明细列表查询</span>
        </div>
      </div>
      <div class="content-main">
        <el-row>
          <el-col :span="8">
            <el-input
              placeholder="请输入用户编号"
              size="mini"
              class="input"
              v-model="clientListQueryForm.userNum"
              clearable
            ></el-input>
          </el-col>
          <el-col :span="8">
            <el-button type="primary" size="mini" @click="getClientListData">查询</el-button>
          </el-col>
        </el-row>
      </div>
    </div>

    <div class="bottom-content content-border">
      <div class="content-title">
        <div class="content-title-left">
          <span class="block"></span>
          <span>第2次停电通知的客户清单</span>
        </div>
      </div>
      <div class="chart-main content-table">
        <bdaTable
          :tableData="clientListData"
          :headerColor="'#1899fa'"
          :isPage="true"
          :pageConfig="clientListPageConfig"
          @changePage="changePage"
        ></bdaTable>
      </div>
    </div>
  </div>
</template>

<script>
import bdaTable from "../../components/bda-table/bda-table";
import Interface from '@/base/mixin/power-sensitive-users';
export default {
  name: "complaintDiagnosis",
  components: {
    bdaTable
  },
  mixins: [Interface],
  props: {},
  data() {
    return {
      options: [],
      valueCode: "",
      value: "",
      selectModeValue: "1",
      selectMode: [
        {
          value: "1",
          label: "或"
        },
        {
          value: "2",
          label: "且"
        },
        {
          value: "3",
          label: "非"
        }
      ],
      //客户第2次通知的可接受度分析
      acceptanceData: {},
      acceptanceQueryForm: {
        current: 1,
        size: 10
      },
      //分页设置
      acceptancePageConfig: {
        name: "acceptance",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //第2次停电通知的客户清单
      clientListData: {},
      clientListQueryForm: {
        userNum: "",
        current: 1,
        size: 10
      },
      //分页设置
      clientListPageConfig: {
        name: "clientList",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      }
    };
  },
  mounted() {
    this.getAcceptanceData()
    this.getClientListData()
  },
  methods: {
    //改变分页
    changePage(data) {
      console.log(data, "data");
      if (data.name === "acceptance") {
        this.acceptanceQueryForm.size = data.size;
        this.acceptanceQueryForm.current = data.current;
        this.getAcceptanceData();
      }
      if (data.name === "clientList") {
        this.clientListQueryForm.size = data.size;
        this.clientListQueryForm.current = data.current;
        this.getClientListData();
      }
    },
    //客户第2次通知的可接受度分析
    getAcceptanceData() {
      this.acceptanceData = {};
      this.getAnalysedData(this.acceptanceQueryForm).then(res => {
        this.acceptancePageConfig.total = res.total;
        this.acceptancePageConfig.size = this.acceptanceQueryForm.size;
        this.acceptancePageConfig.current = this.acceptanceQueryForm.current;
        this.acceptanceData = {
          tableData: [...res.records],
          columnNameData: [
            { prop: "userNum", label: "用户编号" },
            { prop: "userName", label: "用户名称" },
            { prop: "bangPubnum", label: "是否绑定公众号" },
            { prop: "phoneInvalid", label: "联系号码是否失效" },
            { prop: "complain", label: "是否发生过重复抱怨" },
            { prop: "firstInvoke", label: "第一次短信通知是否失败" },
            { prop: "updateTime", label: "更新时间间" }
          ]
        };
      });
    },
    //第2次停电通知的客户清单
    getClientListData() {
      this.clientListData = {};
      this.getPowerCurNotice({
        userNum: this.clientListQueryForm.userNum,
        size: this.clientListQueryForm.size,
        current: this.clientListQueryForm.current
      }).then(res => {
        this.clientListPageConfig.total = res.total;
        this.clientListPageConfig.size = this.clientListQueryForm.size;
        this.clientListPageConfig.current = this.clientListQueryForm.current;
        this.clientListData = {
          tableData: [...res.records],
          columnNameData: [
            { prop: "userNum", label: "用户编号" },
            { prop: "userName", label: "用户名称" },
            { prop: "userAdress", label: "用户地址" },
            { prop: "adress", label: "地区" },
            { prop: "proviceCur", label: "供电所" },
            { prop: "tagUpdateTime", label: "标签更新时间" },
            { prop: "sensitiveLevel", label: "停电敏感级别" },
            { prop: "secondNotice", label: "是否第二次停电通知" },
            { prop: "distWay", label: "停电敏感识别方式" },
            { prop: "sendWay", label: "禁止第二次短信发送的标记方式" }
          ]
        };
      });
    }
  }
};
</script>

<style scoped lang="scss">
.complaintDiagnosis {
  ::v-deep .el-table .warning-row {
    // background: #e7f6f7;
    background: #e9f3fe;
  }

  $Color: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
  margin: 10px 0px 0px;

  .content-border {
    background: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }

  .top-content,
  .bottom-content {
    height: 356px;
    margin-bottom: 20px;
    .content-main {
      margin: 10px;
      .row_form {
        margin: 20px 0px;
        .bda_input {
          width: 100%;
        }
        .bda_select {
          margin-right: 10px;
        }
      }
    }
    .updata {
      margin: 0px 14px;
    }
    .chart-main {
      height: calc(100% - 40px);
      padding: 10px;
      box-sizing: border-box;
    }
  }
  .center-content {
    height: 100px;
    margin-bottom: 20px;

    .content-main {
      padding: 10px;
      box-sizing: border-box;
    }
  }
  .content-title {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    line-height: 40px;
    padding: 10px 5px;
    box-sizing: border-box;
    font-weight: bold;
    color: #1a84f4;

    .block {
      display: inline-block;
      width: 8px;
      height: 16px;
      margin: 0 10px 0 0;
      background-image: $Color;
    }

    .content-title-left {
      display: flex;
      align-items: center;
      margin-left: 10px;
    }
  }
}
</style>
